import React, { Component } from 'react';
import './registration-page.css';
import {fetchRegistrationUserData} from "../../actions/user-actions";
import { connect } from 'react-redux';
import withPizzaDeliveryService from "../hoc/with-pizza-delivery-service";
import compose from "../../utils";
import {Redirect} from "react-router-dom";
import Dialog from 'react-bootstrap-dialog';
import {bindActionCreators} from "redux";
import SignInError from "../sign-in-error";
import RegistrationSuccess from "../registration-success";

class RegistrationPage extends Component {

    state = {
        clientName: '',
        password: ''
    };

    handleSubmit = (e) => {
        e.preventDefault();
        if (this.state.clientName === '' || this.state.password === '') {

            return this.dialog.show({
                title: 'Empty fields',
                body: 'Enter nickname and password',
                bsSize: 'large',
                actions: [
                    Dialog.OKAction()
                ]
            })
        } else {
            this.props.fetchRegistrationUserData(this.state.clientName, this.state.password);
        }
    };

    render() {

        const { isAuthenticated } = this.props;

        if (isAuthenticated) {
            return <Redirect to='/login' />;
        }

        console.log(this.props.signupErrors);

        return (
            <React.Fragment>
            <form className="reg-page__form registration-page__desktop">
                { this.props.signupErrors.length !== 0 ? <SignInError errorBody="Username already exist" errorHeader="Error" /> : null }
                { this.props.signUpSuccess ? <RegistrationSuccess/> : null }
                <h3>Registration below</h3>
                <label className="reg-page__label">
                    <p className="label-txt">ENTER YOUR NAME</p>
                    <input type="text"
                           className="input"
                           onChange={(e) => this.setState({ clientName: e.target.value })}/>
                        <div className="line-box">
                            <div className="line"></div>
                        </div>
                </label>
                <label className="reg-page__label">
                    <p className="label-txt">ENTER YOUR PASSWORD</p>
                    <input type="password"
                           onChange={(e) => this.setState({ password: e.target.value })}
                           className="input"/>
                        <div className="line-box">
                            <div className="line"></div>
                        </div>
                </label>
                <button type="submit" onClick={this.handleSubmit} className="reg-page__button">submit</button>
                <Dialog ref={(component) => { this.dialog = component }} />
            </form>

                <form className="registration-page__mobile">
                    <h3 className="registration-page__mobile_title">Registration below</h3>
                    { this.props.signupErrors.length !== 0 ? <SignInError errorBody="Username already exist" errorHeader="Error" /> : null }
                    { this.props.signUpSuccess ? <RegistrationSuccess/> : null }
                    <div className="form-group-row">
                        <label htmlFor="email_address" className="col-md-4 col-form-label text-md-right">
                            Username
                        </label>
                        <div className="col-md-6">
                            <input
                                type="text"
                                id="email_address"
                                className="form-control"
                                onChange={(e) => this.setState({ clientName: e.target.value })}
                                value={this.state.clientName}
                                name="email-address" required/>
                        </div>
                    </div>

                    <div className="form-group-row">
                        <label htmlFor="password" className="col-md-4 col-form-label text-md-right">
                            Password
                        </label>
                        <div className="col-md-6">
                            <input
                                type="password"
                                id="password"
                                className="form-control"
                                onChange={(e) => this.setState({ password: e.target.value })}
                                value={this.state.password}
                                name="password" required/>
                        </div>
                    </div>
                    <button type="button" onClick={this.handleSubmit} className="btn submit-button__mobile btn-primary">Submit</button>
                </form>
            </React.Fragment>
        );
    };
}

const mapStateToProps = ({ UserReducer: {clientName, signupErrors, signUpSuccess }}) => {
    return { clientName, signupErrors, signUpSuccess };
};

const mapDispatchToProps = (dispatch, { pizzaService }) => {
    return bindActionCreators({
        fetchRegistrationUserData: fetchRegistrationUserData(pizzaService)
    }, dispatch);
};

export default compose(
    withPizzaDeliveryService(),
    connect(mapStateToProps, mapDispatchToProps)
)(RegistrationPage);
