import React, { Component } from 'react';
import './login-page.css';
import './registration-page.css';
import { connect } from 'react-redux';
import compose from '../../utils';
import withPizzaDeliveryService from "../hoc/with-pizza-delivery-service";
import { fetchLoginUserData } from '../../actions/user-actions';
import {Redirect} from "react-router-dom";
import Dialog from 'react-bootstrap-dialog';
import {bindActionCreators} from "redux";
import SignInError from "../sign-in-error";

class LoginPage extends Component {

    state = {
        clientName: '',
        password: ''
    };

    handleSubmit = (e) => {
        e.preventDefault();
        if (this.state.clientName === '' || this.state.password === '') {
            e.preventDefault();
            return this.dialog.show({
                title: 'Empty fields',
                body: 'Enter nickname and password',
                bsSize: 'large',
                actions: [
                    Dialog.OKAction()
                ]
            })
        } else {
            console.log(this.props.signinErrors);
            this.props.fetchUserData(this.state.clientName, this.state.password);
        }
    };

    handlePassword = (e) => {
        this.setState({ password: e.target.value })
    };

    handleUsername = (e) => {
        this.setState({ clientName: e.target.value });
    };

    render() {

        if (window.localStorage.getItem('token')) {
            return <Redirect to="/" exact/>;
        }

        return (
            <React.Fragment>
            <form className="reg-page__form login-page__desktop">
                { this.props.signinErrors.length !== 0 ? <SignInError errorBody="Incorrect username or password" errorHeader="Error" /> : null }
                <h3>Login below</h3>
                <label className="reg-page__label">
                    <p className="label-txt">ENTER YOUR NAME</p>
                    <input type="text"
                           className="input"
                           onChange={(e) => this.handleUsername(e)}
                           value={this.state.clientName}/>
                    <div className="line-box">
                        <div className="line"></div>
                    </div>
                </label>
                <label className="reg-page__label">
                    <p className="label-txt">ENTER YOUR PASSWORD</p>
                    <input type="password"
                           className="input"
                           value={this.state.password}
                           onChange={(e) => this.handlePassword(e)}/>
                    <div className="line-box">
                        <div className="line"></div>
                    </div>
                </label>
                <button className="reg-page__button" onClick={this.handleSubmit}>submit</button>
                <Dialog ref={(component) => { this.dialog = component }} />
            </form>

                <form className="login-page__mobile">
                    { this.props.signinErrors.length !== 0 ? <SignInError errorBody="Incorrect username or password" errorHeader="Error" /> : null }
                    <h3>Log in below</h3>
                    <div className="form-group-row">
                        <label htmlFor="email_address" className="col-md-4 col-form-label text-md-right">
                            Username
                        </label>
                        <div className="col-md-6">
                            <input
                                type="text"
                                id="email_address"
                                className="form-control"
                                onChange={(e) => this.handleUsername(e)}
                                value={this.state.clientName}
                                name="email-address" required/>
                        </div>
                    </div>

                    <div className="form-group-row">
                        <label htmlFor="password" className="col-md-4 col-form-label text-md-right">
                            Password
                        </label>
                        <div className="col-md-6">
                            <input
                                type="password"
                                id="password"
                                className="form-control"
                                onChange={(e) => this.handlePassword(e)}
                                value={this.state.password}
                                name="password" required/>
                        </div>
                    </div>
                    <button type="button" onClick={this.handleSubmit} className="btn submit-button__mobile btn-primary">Log in</button>
                </form>

            </React.Fragment>
        );
    };
}

const mapStateToProps = ({ UserReducer: {signinErrors, clientName, isAuthenticated}, MenuReducer: {loading}}) => {
    return { clientName, signinErrors, isAuthenticated, loading };
};

const mapDispatchToProps = (dispatch, { pizzaService }) => {
    return bindActionCreators({
        fetchUserData: fetchLoginUserData(pizzaService)
    }, dispatch);
};

export default compose(
    withPizzaDeliveryService(),
    connect(mapStateToProps, mapDispatchToProps)
)(LoginPage);
