import React, { Component } from 'react';
import compose from "../../utils";
import withPizzaDeliveryService from "../hoc/with-pizza-delivery-service";
import { fetchUserOrderHistory } from "../../actions/user-actions";
import { connect } from 'react-redux';
import HistoryPageListItem from '../history-list/history-page-list-item';
import Spinner from "../spinner";
import PropTypes from 'prop-types';
import {bindActionCreators} from "redux";
import './history-list.css';
import {Link} from "react-router-dom";

class HistoryList extends Component {

    componentDidMount() {
        const { fetchUserOrderHistory, clientName } = this.props;
        fetchUserOrderHistory(clientName);
    }

    render() {
        const { currentUserOrderHistory, loading } = this.props;

        if (loading) {
            return <Spinner/>
        }

        if (currentUserOrderHistory.length === 0) {
            return(
                <div className="container-fluid mt-1 pt-1 pt-md-0 mt-md-1">
                    <div className='row'>
                        <div className="col-md-3 diagonal ">
                            <div className="card border-0 mt-md-5 pt-md-5 bg-transparent">
                                <div className="card-body bg-transparent text-center">
                                    <h4 className="card-title text-dark">Your history is empty</h4>
                                    <p className="card-text text-secondary">Order some pizza</p>
                                    <div className='text-center'><Link to="/" className="btn btn-danger text-white">Go to main page</Link></div>
                                </div>
                            </div>
                        </div>
                        <div className="col-md-9  diagonal diagonal-image">
                            {/*<div className="diagonal-text text-white p-md-4">*/}
                            {/*    <h3>pizza delivery</h3>*/}
                            {/*    <p>*/}
                            {/*    </p>*/}

                            {/*</div>*/}
                        </div>
                    </div>

                </div>
            );
        }

        return(
            <ul className="list-group">
                {
                    currentUserOrderHistory.map(item => {
                        return(
                            <li className="list-group-item" key={item.orderId}>
                                <HistoryPageListItem historyData={item} />
                            </li>
                        );
                    })
                }
            </ul>
        )
    }

    static get propTypes() {
        return {
            fetchUserOrderHistory: PropTypes.func.isRequired,
            currentUserOrderHistory: PropTypes.array,
            loading: PropTypes.bool,
            clientName: PropTypes.string
        };
    }
}

const mapStateToProps = ({ UserReducer: {clientName, loading, currentUserOrderHistory} }) => {
    return { clientName, currentUserOrderHistory, loading };
};

const mapDispatchToProps = (dispatch, { pizzaService }) => {
    return bindActionCreators({
        fetchUserOrderHistory: fetchUserOrderHistory(pizzaService)
    }, dispatch);
};

export default compose(
    withPizzaDeliveryService(),
    connect(mapStateToProps, mapDispatchToProps)
)(HistoryList);
